body {
  font-family: Arial, sans-serif;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.table-container {
  width: 45%;
  padding-right: 20px;
  overflow-y: auto;
  max-height: 500px;
}

.map-container {
  width: 50%;
  position: relative; /* Ensure map container is positioned correctly */
}

table {
  width: 100%;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid black;
}

th, td {
  padding: 8px;
  text-align: left;
}

#map {
  width: 100%;
  height: 500px;
  position: relative; /* Ensure map container is positioned correctly */
  overflow: hidden;
}

.custom-marker {
  position: absolute; /* Make sure markers are positioned relative to the map container */
  background-color: #3FB1CE;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}


.form-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.form-container select {
  margin-right: 10px;
}
